<template>
	<view class="cartContainer">
		<block v-if="cartList.length">
			<!-- 购物车列表 -->
			<view class="cartList">
				<view class="cartItem" v-for="(item,index) in cartList" :key="item.id">
					<text class='iconfont icon-yuedu' :class="{selected:item.selected}" @click="changeSelected(index)"></text>
					<view class="shopItem">
						<image class="shopImg" :src="item.listPicUrl" mode=""></image>
						<view class="shopInfo">
							<text>{{item.name}}</text>
							<text class="price">￥{{item.retailPrice}}</text>
						</view>
					</view>
					<!-- 控制数量 -->
					<view class="countCtrl">
						<text class="add" @click="changeNum(true,index)"> + </text>
						<text class="count"> {{item.count}} </text>
						<text class="del" @click="changeNum(false,index)"> - </text>
					</view>
				</view>

			</view>
			<!-- 底部下单 -->
			<view class="cartFooter">
				<text class='iconfont icon-yuedu' :class="{selected: isAllSelected}" @click="changeAllSelected(!isAllSelected)"></text>
				<text class="allSelected">已选 {{cartList.filter(item=>item.selected).length}}</text>
				<view class="right">
					<text class="totalPrice">合计: ￥{{money}}</text>
					<text class="preOrder">下单</text>
				</view>
			</view>
		</block>
		<block v-else>
			<view class="hint">
				<image class="cartImg" src="http://yanxuan-static.nosdn.127.net/hxm/yanxuan-wap/p/20161201/style/img/icon-normal/noCart-d6193bd6e4.png?imageView&type=webp" mode=""></image>
				购物车还是空的，赶紧去购物吧
			</view>
		</block>
	</view>
</template>

<script>
	import {
		mapMutations,
		mapState,
		mapGetters
	} from 'vuex'
	export default {
		data() {
			return {
				userInfo: {}
			};
		},
		computed: {
			...mapState({
				cartList: state => state.cartModule.cartList
			}),
			...mapGetters(['isAllSelected']),
			money() {
				let sum = 0;
				for (let i = 0; i < this.cartList.length; i++) {
					if (this.cartList[i].selected) {
						sum += this.cartList[i].count * this.cartList[i].retailPrice
					}
				}
				return sum;
			}
		},
		methods: {
			...mapMutations({
				changeCartNumMutation: 'changeCartNumMutation',
				changeSelectedMutation: 'changeSelectedMutation',
				changeAllSelectedMutation:'changeAllSelectedMutation'
			}),
			changeNum(flag, index) {
				let obj = {
					flag,
					index,
					confirmText: this.confirmText
				}
				this.changeCartNumMutation(obj)
			},
			changeSelected(index) {
				this.changeSelectedMutation(index)
			} ,
			changeAllSelected(flag){
				this.changeAllSelectedMutation(flag
				)
			}
		}
	}
</script>

<style lang="stylus">
	/* iconfont 公共样式 */
		.cartImg
			display block
			width 248upx
			height 248upx
			margin 10rpx auto
			
			
			
		.cartContainer
			position relative
			background #f4f4f4
			height 100%
			.title
				font-size 32upx
				line-height 80upx
				margin-left 30upx
			.header
				display flex
				background #eee
				text
					width 33.333%
					height 80upx
					line-height 80upx
					text-align center
					font-size 26upx
			.contentContainer
				
				button
					width 480upx
					height 92upx
					background #DD1A21
					color #fff
					font-size 32upx
					line-height 92upx
				.addMore
					text-align center
					font-size 26upx
					color #7f7f7f
					line-height 60rpx
			.cartList
				.cartItem
					position relative
					display flex
					height 172upx
					background #fff
					margin-top 20upx
					padding 20upx
					.iconfont
						font-size 40upx
						line-height 172upx
						margin 0 40upx
						color #999
						&.selected
							color: #BB2C08
					.shopItem
						display flex 
						.shopImg
							width 172upx
							height 172upx
							background #eee
						.shopInfo
							font-size 28upx
							display flex
							flex-direction column
							margin-left 20upx
							text
								line-height 60upx
							.price
								color: #BB2C08
					.countCtrl
						position absolute
						right 20upx
						bottom 30upx
						text
							display inline-block
							width 60upx
							height 50upx
							text-align center
							line-height 50upx
							border 1upx solid #EEEEEE
							&:nth-child(2)
								border none
								border-top 1upx solid #EEEEEE
								border-bottom 1upx solid #EEEEEE
			.cartFooter
				position absolute
				display flex	
				bottom 0
				height 96upx
				line-height 96upx
				width 100%
				background #fff
				font-size 30upx
				.iconfont
					font-size 40upx
					margin 0 20upx
					color: #999
					&.selected
						color: #BB2C08
				.right 
					height 96upx
					position absolute
					right 0
					.totalPrice
						display inline-block
						height 96upx
						line-height 96upx
					.preOrder
						display inline-block
						background #DD1A21
						width 225upx
						height 96upx
						line-height 96upx
						text-align center
						font-size 28upx
						color #fff
						margin-left 30upx
			.hint
				position absolute
				left 50% 
				top 50%
				transform translateX(-50%) translateY(-50%)
				text-align center
				font-size 26upx
				// background-color #007AFF
</style>
